"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Header } from "@/components/header"
import { dashboardService } from "@/services/dashboardService"
import { formatarMoeda } from "@/lib/utils"
import { AlertCircle, DollarSign, Package, Users } from "lucide-react"
import Link from "next/link"
import { DashboardQueue } from "@/components/dashboard/dashboard-queue"
import { RecentSales } from "@/components/dashboard/recent-sales"

// Dados iniciais
const initialData = {
  totalSales: 0,
  salesGrowth: 0,
  totalCustomers: 0,
  customersGrowth: 0,
  totalProducts: 0,
  lowStockProducts: 0,
  queueWaiting: 0,
  queueInService: 0,
}

export default function DashboardPage() {
  const [data, setData] = useState(initialData)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true)

        // Buscar dados do dashboard
        const dashboardData = await dashboardService.getDashboardData()

        // Atualizar o estado com os dados
        setData({
          ...dashboardData,
        })

        setError(null)
      } catch (err) {
        console.error("Erro ao carregar dados do dashboard:", err)
        setError("Não foi possível carregar os dados do dashboard. Tente novamente mais tarde.")
      } finally {
        setLoading(false)
      }
    }

    fetchData()

    // Atualizar a cada 30 segundos
    const interval = setInterval(fetchData, 30000)
    return () => clearInterval(interval)
  }, [])

  if (loading) {
    return (
      <div className="min-h-screen flex flex-col">
        <Header />
        <main className="flex-1 flex items-center justify-center">
          <p>Carregando dados do dashboard...</p>
        </main>
      </div>
    )
  }

  if (error) {
    return (
      <div className="min-h-screen flex flex-col">
        <Header />
        <main className="flex-1 flex flex-col items-center justify-center">
          <div className="bg-red-50 p-4 rounded-md text-red-600 max-w-md text-center">
            <AlertCircle className="mx-auto mb-2 h-10 w-10" />
            <p>{error}</p>
          </div>
        </main>
      </div>
    )
  }

  return (
    <div className="min-h-screen flex flex-col">
      <Header />
      <main className="flex-1">
        <div className="container mx-auto p-4 space-y-6">
          <h1 className="text-2xl font-bold">Dashboard</h1>

          {/* Cards de resumo */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Card>
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">Vendas Totais</CardTitle>
                <DollarSign className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{formatarMoeda(data.totalSales)}</div>
                <p className="text-xs text-muted-foreground">
                  {data.salesGrowth > 0 ? "+" : ""}
                  {data.salesGrowth}% em relação ao mês anterior
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">Estoque</CardTitle>
                <Package className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{data.totalProducts}</div>
                <p className="text-xs text-muted-foreground">{data.lowStockProducts} produtos com estoque baixo</p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium">Clientes</CardTitle>
                <Users className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{data.totalCustomers}</div>
                <p className="text-xs text-muted-foreground">
                  {data.customersGrowth > 0 ? "+" : ""}
                  {data.customersGrowth}% em relação ao mês anterior
                </p>
              </CardContent>
            </Card>
          </div>

          {/* Tabelas de detalhes */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Vendas Recentes */}
            <Card>
              <CardHeader>
                <CardTitle>Vendas Recentes</CardTitle>
                <CardDescription>As últimas 5 vendas realizadas no sistema.</CardDescription>
              </CardHeader>
              <CardContent>
                <RecentSales />
                <div className="mt-4 text-right">
                  <Link href="/vendas" className="text-sm text-blue-600 hover:underline">
                    Ver todas as vendas →
                  </Link>
                </div>
              </CardContent>
            </Card>

            {/* Produtos com Estoque Baixo */}
            <Card>
              <CardHeader>
                <CardTitle>Produtos com Estoque Baixo</CardTitle>
                <CardDescription>Produtos que precisam ser repostos em breve.</CardDescription>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Produto</TableHead>
                      <TableHead>Preço</TableHead>
                      <TableHead>Quantidade</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {/* Conteúdo da tabela de produtos com estoque baixo */}
                    <TableRow>
                      <TableCell colSpan={3} className="text-center py-4">
                        Carregando produtos...
                      </TableCell>
                    </TableRow>
                  </TableBody>
                </Table>
                <div className="mt-4 text-right">
                  <Link href="/estoque" className="text-sm text-blue-600 hover:underline">
                    Gerenciar estoque →
                  </Link>
                </div>
              </CardContent>
            </Card>

            {/* Clientes na Fila */}
            <Card>
              <CardHeader>
                <CardTitle>Clientes na Fila</CardTitle>
                <CardDescription>Clientes aguardando atendimento.</CardDescription>
              </CardHeader>
              <CardContent>
                <DashboardQueue />
                <div className="mt-4 text-right">
                  <Link href="/fila" className="text-sm text-blue-600 hover:underline">
                    Gerenciar fila →
                  </Link>
                </div>
              </CardContent>
            </Card>

            {/* Serviços em Andamento */}
            <Card>
              <CardHeader>
                <CardTitle>Serviços em Andamento</CardTitle>
                <CardDescription>Serviços que estão sendo realizados no momento.</CardDescription>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Cliente</TableHead>
                      <TableHead>Serviço</TableHead>
                      <TableHead>Início</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {/* Conteúdo da tabela de serviços em andamento */}
                    <TableRow>
                      <TableCell colSpan={3} className="text-center py-4">
                        Nenhum serviço em andamento no momento
                      </TableCell>
                    </TableRow>
                  </TableBody>
                </Table>
                <div className="mt-4 text-right">
                  <Link href="/servicos" className="text-sm text-blue-600 hover:underline">
                    Ver todos os serviços →
                  </Link>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </main>
    </div>
  )
}

